import React, { useState, createRef } from "react";
//import Field from "./components/Field";
import Palette from "./components/Palette";
import Submit from "./components/Submit";
import { postData } from "./functions/postData";

function App() {
  const [activeFieldItem, setActiveFieldItem] = useState({});
  const [activePaletteItem, setActivePaletteItem] = useState({});
  const [colors, setColors] = useState({});

  const fieldRef = createRef();

  function onSelectFieldItem(e, index) {
    const data = {
      index,
      x: e.target.offsetTop - 1 - fieldRef.current.offsetTop,
      y: e.target.offsetLeft - 1 - fieldRef.current.offsetLeft,
    };

    index === activeFieldItem.index
      ? setActiveFieldItem({})
      : setActiveFieldItem(data);
  }

  function onSelectPaletteItem(index, color) {
    const data = {
      index,
      color,
    };

    index === activePaletteItem.index
      ? setActivePaletteItem({})
      : setActivePaletteItem(data);
  }

  function onSubmit() {
    const data = {
      color: activePaletteItem.color,
      x: activeFieldItem.x,
      y: activeFieldItem.y,
    };

    console.log(data);

    postData("/check", data).then((response) => {
      console.log(response);
    });

    setColors(
      Object.assign(
        {
          [activeFieldItem.index]: activePaletteItem.color,
        },
        colors
      )
    );

    setActivePaletteItem({});
    setActiveFieldItem({});
  }

  return (
    <div className="App">
      <div className="container">
        <div className="row mb-2">

          <Palette
            active={activePaletteItem.index}
            onSelect={onSelectPaletteItem}
          />
        </div>
        <div className="row">
          <Submit onSubmit={onSubmit} />
        </div>
      </div>
    </div>
  );
}

export default App;
