import React from "react";
import PaletteItem from "./PaletteItem";
import PropTypes from "prop-types";

function Palette({ active, onSelect }) {
  function generateItems() {
    const items = [];

    for (let r = 0, max = 4, i = 0; r <= max; r++) {
      for (let g = 0; g <= max; g++) {
        for (let b = 0; b <= max; b++, i++) {
          const color = `rgba(${Math.round((r * 255) / max)}, ${Math.round(
            (g * 255) / max
          )}, ${Math.round((b * 255) / max)})`;
          items.push(
            <PaletteItem
              key={i}
              index={i}
              onSelect={onSelect}
              active={i === active}
              color={color}
            />
          );
        }
      }
    }

    return items;
  }

  const items = generateItems();

  return <div className="Palette">{items}</div>;
}

Palette.propTypes = {
  active: PropTypes.any,
  onSelect: PropTypes.func.isRequired,
};

export default Palette;
